.typed-text{margin:0;display:inline-block;color:var(--primary-color);font-size:16px;line-height:40px;height:20px;font-weight:400;z-index:3}.typed-cursor{width:2px;color:#0b0b0b;font-size:16px;line-height:20px;height:20px;z-index:3}

.social{position:relative;margin:16px 0 0}.social>a{margin:0 6px;display:inline-block;vertical-align:middle}.social>a:hover>span{color:var(--primary-color)}.social>a>span{-webkit-font-smoothing:antialiased;display:inline-block;font-style:normal;font-feature-settings:normal;font-variant:normal;text-rendering:auto;line-height:1;font-size:16px;color:#323232;transition:all .3s ease 0s;-moz-transition:all .3s ease 0s;-webkit-transition:all .3s ease 0s}@media only screen and (max-width: 56.25em){.social>a>span{font-size:28px}}

.info{margin-top:16px;z-index:4;font-size:12px;display:flex;align-items:center}.info .property{display:flex;align-items:center;margin-right:8px;padding:1px 8px;background:var(--primary-color);font-size:14px;line-height:20px;color:white}.info .property>strong{text-transform:capitalize}.info .property>i{-webkit-font-smoothing:antialiased;display:inline-block;font-style:normal;font-feature-settings:normal;font-variant:normal;text-rendering:auto;line-height:1;font-size:16px;transition:all .3s ease 0s;-moz-transition:all .3s ease 0s;-webkit-transition:all .3s ease 0s;margin-right:5px}.info>span{text-transform:capitalize}

.skills{text-align:left;margin-top:30px}.skills>h6{margin:10px 0}.skills ul{list-style-type:none;margin:0;padding:0}.skills ul li{border:1px solid #2D2747;border-radius:2px;display:inline-block;font-size:12px;margin:0 7px 7px 0;padding:7px;cursor:pointer;transition:transform .3s ease-in}.skills ul li:hover{transform:scale(1.1) translateY(-2px)}

.menu{padding:0;list-style:none;z-index:22;margin:0;grid-area:hd;display:flex}.menu>li{display:block;overflow:hidden;transition:color .3s ease 0s;-moz-transition:color .3s ease 0s;-webkit-transition:color .3s ease 0s;flex-grow:1;position:relative}.menu>li:hover>a,.menu>li:hover span{color:var(--primary-color)}.menu>li:not(:last-child)::before{content:'';position:absolute;right:0;bottom:0;width:1px;height:100%;background:radial-gradient(ellipse at center, #ddd 0%, rgba(255,255,255,0) 70%);z-index:222}.menu>li>a{text-decoration:none;padding:15px 16.5px;position:relative;overflow:hidden;height:auto;color:#323232;text-align:center;background:#fff;font-size:12px;font-weight:500;text-transform:uppercase;transition:all .3s ease 0s;-moz-transition:all .3s ease 0s;-webkit-transition:all .3s ease 0s;display:flex;flex-direction:column;justify-content:center}.menu>li>a span{margin:0 auto;position:relative;top:0;left:0;display:block;height:30px;font-size:14px;color:#323232;line-height:30px;border-radius:30px;transition:all .3s ease 0s;-moz-transition:all .3s ease 0s;-webkit-transition:all .3s ease 0s}.active{color:var(--primary-color) !important}.active span{color:var(--primary-color) !important}


.title{margin:0;padding:0 0 30px;position:relative;font-size:20px;color:#323232;line-height:21px;min-height:51px;font-weight:600;z-index:2;display:flex;align-items:center}.title>h3{margin:0 0 0 10px}.title>i{font-size:28px;color:var(--primary-color)}.title::after{content:'';position:absolute;left:-30px;right:0;bottom:0;height:1px;background:radial-gradient(ellipse at left, #ddd 0%, rgba(255,255,255,0) 70%)}


.resume-item{padding:30px 0;position:relative}.resume-item:not(:last-child)::after{content:'';position:absolute;left:0;bottom:0;width:100%;height:1px;background:radial-gradient(ellipse at center, #ddd 0%, rgba(255,255,255,0) 70%)}.resume-item.not-last::after{display:none}.resume-item__experience{display:flex;align-items:center;justify-content:space-between}.resume-item__experience>img{width:80px;height:80px;border-radius:50%}.resume-item__date{margin:0 0 11px;padding:0 5px;position:relative;display:inline-block;vertical-align:top;font-size:13px;line-height:18px;color:#ccc;font-weight:400;border:1px solid #eee}.resume-item__company p{margin:0 0 11px;font-size:17px;color:#323232;font-weight:500}.resume-item__company h3{margin:0 0 11px;font-size:14px;color:#999;font-weight:400}

.work-item{text-align:center;padding:30px}.work-item__slider img{width:100%;height:300px}.work-item h3{padding:0;display:block;font-size:17px;color:#323232;font-weight:500;text-transform:none;word-break:break-word;transition:all .3s ease 0s}.work-item__links{width:100%;display:flex;flex-direction:row;z-index:10;position:relative}.work-item__links::after{content:'';position:absolute;left:0;bottom:0;width:100%;height:1px;background:radial-gradient(ellipse at center, #ddd 0%, rgba(255,255,255,0) 70%)}.work-item__links a{position:relative;display:block;width:100%;padding:10px;font-size:12px;color:#323232;text-align:center;text-decoration:none;text-transform:uppercase;border:none;cursor:pointer;display:flex;align-items:center}.work-item__links a:hover{color:var(--primary-color)}.work-item__links a:first-child::after{content:'';position:absolute;right:0;top:0;width:1px;height:100%;background:radial-gradient(ellipse at bottom, #ddd 0%, rgba(255,255,255,0) 70%)}.work-item__links a i{margin-right:10px}

.owl-dots{position:absolute;bottom:-1.2rem;left:0}.owl-dots{width:100%;text-align:center}.owl-dots .active.owl-dot{background-color:var(--primary-color)}.owl-dots .owl-dot{display:inline-block;zoom:1;margin:8px 4px;border-radius:10px;background-color:#fff;padding:4px;border:solid 1px #e9e9e9;cursor:pointer}.owl-dots .owl-dot:focus{outline:none}

